<template>
	<div class="exposure-list">
		<div class="exposure-box" v-for="item in options.option">
			<div class="exposure-top">
			<p><span :class="[item.isrise ? 'rise':'drop','rose']">{{ item.rise }}</span>{{ item.title }}</p>
			<p><span :class="[item.isrise ? 'rise':'drop','price']">{{ item.mark }}{{ item.price }}</span></p>
		</div>
		<div class="exposure-bot">
			<div class="exposure">
				<p v-for="exposure in item.exposure"><span>{{ exposure.title }}</span><span :class="[exposure.mark ? 'sybmol':'',exposure.isrise ? 'rise':'drop']">{{ exposure.price }}<i v-if="exposure.mark" :class="[exposure.isrise ? 'fa-long-arrow-up':'fa-long-arrow-down','fa price']"></i></span></p>
			</div>
			<div class="state">
				<div><p>止盈：<span>没设</span></p><p>止损：<span>20%</span><a href="javascript:;"><i class="fa fa-pencil"></i></a></p></div>
				<div><a href="javascript:;" :class="item.isrise ? 'rise':'drop'">平仓</a></div>
			</div>
		</div>
		</div>
	</div>
</template>

<script>
	export default{
		data(){
			return {
				options:{
					option:[]
				}	
			}
		},
		created(){
			let num = parseInt(Math.random()*20);
			let obj = null;
			for(let i = 0;i <= num-1;i++){
				if((Math.random()-0.5)>=0){
					obj = {
						rise: '涨',
						title: '10克黄金',
						isrise: true,
						mark: '+',
						price: (parseInt(Math.random()*2000)/10).toFixed(2),
						exposure:[
							{title: '建仓价',price: parseInt(Math.random()*3000),mark: false},
							{title: '现金',price: parseInt(Math.random()*3000),mark: true,isrise: true},
							{title: '保本价',price: parseInt(Math.random()*3000),mark: false},
							{title: '手数',price: parseInt(Math.random()*3000),mark: false},
						]
					}
				}else{
					obj = {
						rise: '跌',
						title: '100克白银',
						isrise: false,
						mark: '-',
						price: (parseInt(Math.random()*2000)/100).toFixed(2),
						exposure:[
							{title: '建仓价',price: parseInt(Math.random()*3000),mark: false},
							{title: '现金',price: parseInt(Math.random()*3000),mark: true,isrise: false},
							{title: '保本价',price: parseInt(Math.random()*3000),mark: false},
							{title: '手数',price: parseInt(Math.random()*3000),mark: false},
						]
					}
				}
				this.options.option.push(obj);
			}
		}
	}
</script>

<style scoped>
	.exposure-box{
		background-color: #fff;
		padding-bottom: 0.28rem;
		margin-bottom: 0.133333rem;
	}
	.exposure-top{
		display:-webkit-box;
	    display:-webkit-flex;
	    display:-ms-flexbox;
	    display:flex;
	    justify-content: space-between;
	    align-items: center;
	    padding: 0.24rem 0.36rem 0.28rem 0.4rem;
	    border-bottom: 0.053333rem solid #eee;
	}
	.exposure-top p{
		font-size: 0.4rem;
	}
	.exposure-top span.rose{
		display: inline-block;
		padding: 0.053333rem 0.1333333rem;
		font-size: 0.36rem;
		color: #fff;
		border-radius: 0.053333rem;
		-moz-border-radius: 0.053333rem;
		-webkit-border-radius: 0.053333rem;
		margin-right: 0.16rem;
	}
	.exposure-top span.price{
		font-size: 0.48rem;
		font-weight: 600;
	}
	.exposure-top span.price.rise{
		color: #fa3535;
	}
	.exposure-top span.price.drop{
		color: #21cf77;
	}
	.exposure-top span.rose.rise{
		background-color: #fa3535;
	}
	.exposure-top span.rose.drop{
		background-color: #21cf77;
	}
	.exposure-bot .exposure{
		display:-webkit-box;
	    display:-webkit-flex;
	    display:-ms-flexbox;
	    display:flex;
	    padding: 0.28rem 0;
	}
	.exposure-bot .exposure p{
		flex-grow: 1;
	}
	.exposure span{
		display: block;
		line-height: 0.58333rem;
	}
	.exposure span:first-child{
		color: #666;
		font-size: 0.373333rem;
	}
	.exposure span:last-child{
		font-size: 0.373333rem;
	}
	.exposure p span.sybmol{
		position: relative;
	}
	.exposure p span.sybmol.rise{
		color: #fa3535;
	}
	.exposure p span.sybmol.drop{
		color: #21cf77;
	}
	.exposure p i.fa.price{
		position: absolute;
		font-size: 0.1rem;
	}
	.state,.state div:first-child{
		display:-webkit-box;
	    display:-webkit-flex;
	    display:-ms-flexbox;
	    display:flex;
	}
	.state{
		padding: 0 0.36rem 0 0.4rem;	
		font-size: 0.373333rem;	
		justify-content: space-between;
	}
	.state div:last-child a{
		border-radius: 0.2666667rem;
		-moz-border-radius: 0.2666667rem;
		-webkit-border-radius: 0.2666667rem;
		display: block;
		width: 2.1866667rem;
		padding: 0.2rem 0;
	}
	.state div:last-child a.rise{
		color: #fa3535;
		border: 0.0266666667rem solid #fa3535;
	}
	.state div:last-child a.drop{
		color: #21cf77;
		border: 0.0266666667rem solid #21cf77;
	}
	.state div p{
		line-height: 0.8rem;
		margin-right: 0.62666667rem;
	}
	.state div p:last-child{
		margin-right: 0;
	}
	.state div p:last-child a{
		margin-left: 0.12rem;
		color: #fa3535;
		font-size: 0.5rem;
	}
</style>


