<template>
	<div class="my-info">
		<ul>
			<li><a href="" class="head_icon"><img src="../images/head_icon@3x.png" alt=""></a></li>
			<li>
				<div class="info-right">
					<div class="info-top">
						<ul>
							<li><span>余额：<i>{{ infoData.balance }}</i>元</span></li>
							<li><a href="" class="recharge-btn">充值</a><a href="" class="delivery-btn">交割</a></li>
						</ul>
					</div>
					<div class="info-bot">
						<ul>
							<li><span>体验券：<i>{{ infoData.coupon }}</i>张</span></li>
							<li><span :class="infoData.isProfit ? 'rise' : 'drop'">浮动盈亏：<i>{{ infoData.symbol }}{{ infoData.profit }}</i>元</span></li>
						</ul>
					</div>
				</div>
			</li>
		</ul>
	</div>
</template>

<script>
	export default{
		name: 'my-info',
		data(){
			return{
				infoData:{
					balance: '100.00',
					coupon: '8',
					symbol: '+',
					profit: '10.20',
					isProfit: true
				}
			}
		},
		created(){
			let _profit = (parseInt((Math.random()-0.5)*20*10)/10).toFixed(2);
			this.infoData.balance = (parseInt(Math.random()*200*100)/100).toFixed(2);
			this.infoData.coupon = parseInt(Math.random()*100);
			if(_profit > 0){
				this.infoData.profit = _profit;
				this.infoData.symbol = '-';
				this.infoData.isProfit = false;
			}else{
				this.infoData.symbol = '+';
				this.infoData.isProfit = true;
			}
		}
	}
</script>

<style scoped>
	.my-info{
		border-bottom: 1px solid #F4F4F4;
	}
	.my-info i{
		font-style: normal;
		color:  #FA3535;
	}
	.my-info .drop i{
		color: #21CF77;
	}
	.my-info ul{
		display: -webkit-box;      /* OLD - iOS 6-, Safari 3.1-6 */
		display: -moz-box;         /* OLD - Firefox 19- (buggy but mostly works) */
		display: -ms-flexbox;      /* TWEENER - IE 10 */
		display: -webkit-flex;     /* NEW - Chrome */
		display: flex;             /* NEW, Spec - Opera 12.1, Firefox 20+ */
		padding: 0.16rem;
	}
	.my-info ul li{
		display: -webkit-box;      /* OLD - iOS 6-, Safari 3.1-6 */
		display: -moz-box;         /* OLD - Firefox 19- (buggy but mostly works) */
		display: -ms-flexbox;      /* TWEENER - IE 10 */
		display: -webkit-flex;     /* NEW - Chrome */
		display: flex;             /* NEW, Spec - Opera 12.1, Firefox 20+ */
		font-size: 0.4rem;
	}
	.my-info ul li:nth-child(2){
		flex-grow: 2;
	}
	.my-info .head_icon{
		width: 1.44rem;
		height: 1.44rem;
		border-radius: 50%;
		overflow: hidden;
	}
	.my-info .head_icon img{
		display: block;
		width: 1.44rem;
		height: 1.44rem;
	}
	.info-right{
		display: -webkit-box;
		display: -moz-box;
		display: -ms-flexbox;
		display: -webkit-flex;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		flex-grow: 1;
		padding: 0.08rem 0; 
		margin-left: 0.32rem
	}
	.info-right ul{
		padding: 0;
	}
	.info-right ul li{
		flex-grow: 1;
		line-height: 20px;
	}
	.info-right li a{
		display: inline-block;
		padding: 0 0.24rem;
		line-height: 20px;
		background-color: #FA3535;
		color: #fff;
		border-radius: 2.64rem;
		margin-right: 0.32rem;
	}
	.info-right li a:last-child{
		margin-right: 0;
		background-color: #21CF77;
	}
	.info-top ul li:last-child{
		margin-left: 1.04rem;
	}
	.info-bot ul li:last-child{
		margin-left: 1.04rem;
	}
</style>


