<template>
	<div class="detail">
		<div class="detail-left">
			<p><span v-bind:class="options.isRise ? 'rise' : 'drop'">{{ options.price }}<i :class="options.isRise ? 'fa fa-long-arrow-up' : 'fa fa-long-arrow-down'"></i></span></p>
			<p>白银价格（元/千克）</p>
		</div>
		<div class="detail-right">
			<div class="range"><p>最高：<span class="max">{{ options.maxprice }}</span></p><p>昨收：<span>{{ options.takeIn }}</span></p></div>
			<div class="range"><p>最低：<span class="min">{{ options.minPrice }}</span></p><p>今开：<span>{{ options.nowPrice }}</span></p></div>
		</div>
	</div>
</template>	
<script>
	export default{
		name: 'detail_price',
		data() {
			return{
					options:{
						price: 3781,
						isRise: true,
						maxprice: 3890,
						minPrice: 3890,
						takeIn: 3890,
						nowPrice: 3890
					}
			}
		},
		created() {
			// this.classObject2.min = true
			this.options.price = parseInt((Math.random()-0.5)*3000);
			this.options.maxprice = parseInt((Math.random())*3000);
			this.options.minPrice = parseInt((Math.random())*3000);
			this.options.takeIn = parseInt((Math.random())*3000);
			this.options.nowPrice = parseInt((Math.random())*3000);
			if(this.options.price > 0){
				this.options.isRise = true;
			}else{
				this.options.isRise = false;
			}
		}
	}
</script>
<style scoped>
	.detail,.detail-right,.range{
		display: -webkit-box;
		display: -moz-box;
		display: -ms-flexbox;
		display: -webkit-flex;
		display: flex;
	}
	.detail{
		padding: 0.24rem;
	}
	.detail .detail-left .rise{
		color: #fa3535;
	} 
	.detail .detail-left .drop{
		color: #21CF77;
	} 
	.range p{
		margin-right: 0.48rem;
	}
	.range p:last-child{
		margin-right: 0;
	}
	.detail-right{
		flex-direction: column;
		justify-content: space-between;
	}
	.detail-right p{
		font-size: 0.347rem;
	}
	.detail-left,.detail-right{
		flex-grow: 1;
	}
	.detail-left p span{
		font-size: 0.587rem;
	}
	p{
		font-size: 0.373rem;
		text-align: left;
	}
	.detail-left span{
		position: relative;
	}
	.detail-left i{
		position: absolute;
		top: 0;
		right: -10px;
		font-size: 0.4rem;
	}
</style>


