</template>

<script>
	export default{
		data(){
			return {
				data:{
					trade: 193754,
					profit: 125456512.00
				}
			}
		}
	}	
</script>

<style scoped>
	.information{
		padding: 0.16rem 0;	
	}
	.information p span,.information p{
		font-size: 0.34667rem;
	}
	.information p span,.information p i{
		color: #fa3535;
	}
	.information p i{
		font-size: 0.5rem;
		margin-right: 0.16rem;
	}
</style>


