<template>
	<div id="info" class="info">
		<div>
			<Info></Info>
			<Conent></Conent>
		</div>
		<Information></Information>
	</div>
</template>
<script>
	import Hello from '../Hello'
	import Info from './info'
	import Conent from './conent'
	import Information from './information'
	
	export default{
		name: 'my-info',
		components: {
			Info,Hello,Conent,Information
		}
	}
</script>

<style scoped>
	.info{
		display:-webkit-box;
	    display:-webkit-flex;
	    display:-ms-flexbox;
	    display:flex;
	    flex-direction: column;
	    justify-content: space-between;
	    height: 100%;
	    background-color: #fff;
	}
</style>


