<template>
	<div class="percent-box">
		<div class="percent">
			<span class="rise"></span><span class="drop"></span>
		</div>
		<div class="btn">
			<a href="javascript:;" class="rise-btn">买涨<i class="fa fa-thumbs-o-up"></i></a>
			<a href="javascript:;" class="drop-btn">买跌<i class="fa fa-thumbs-o-down"></i></a>
			<div class="auto">
				<a href="javascript:;" class="granary-btn">一键<br/>满仓</a>
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		data(){
			return {
				percent: '70'
			}
		},
		mounted(){

		}
	}
</script>

<style scoped>
	.percent-box{
		margin: 0 0.267rem;
	}
	.percent{
		line-height: 0.16rem;
		border-radius: 0.16rem;
		-moz-border-radius: 0.16rem; /* Firefox */
		-webkit-border-radius: 0.16rem; /* Safari 和 Chrome */
		overflow: hidden;
	}
	.percent span{
		height: 0.16rem;
		display: inline-block;
	}
	.rise{
		background-color: #fa3535;
		width: 70%;
	}
	.drop{
		background-color: #21cf77;
		width: 30%;
	}
	.btn{
		margin-top: 0.5067rem;
		font-size: 0.42667rem;
		display:-webkit-box;
	    display:-webkit-flex;
	    display:-ms-flexbox;
	    display:flex;
	    justify-content: center;
	    align-items: center;
	}
	.auto{
		display: table;
	}
	.btn a{
		color: #fff;
		margin-right: 0.533rem;
	}
	.btn a i{
		margin-left: 0.08rem;
	}
	.btn a.granary-btn{
		font-size: 0.34667rem;
		display: table-cell;
		vertical-align: middle;
		text-align: center;
		height: 1.3333rem;
		width: 1.3333rem;
		margin-right: 0;
		background-color: #f5b515;
		border-radius: 50%;
		-moz-border-radius: 50%;
		-webkit-border-radius: 50%;
		-webkit-box-shadow: 0 0.0533334rem 0.08rem rgba(245,181,21,0.5);  
		-moz-box-shadow: 0 0.0533334rem 0.08rem rgba(245,181,21,0.5);  
		box-shadow: 0 0.0533334rem 0.08rem rgba(245,181,21,0.5);
	}
	.btn a.rise-btn,.btn a.drop-btn{
		display: inline-block;
		width: 3.2rem;
		height: 1.12rem;
		line-height: 1.12rem;
		border-radius: 2.72rem;
		-moz-border-radius: 2.72rem;
		-webkit-border-radius: 2.72rem;
	}
	.btn a.rise-btn{
		background-color: #fa3535;
		-webkit-box-shadow: 0 0.0533334rem 0.08rem rgba(250,53,53,0.5);  
		-moz-box-shadow: 0 0.0533334rem 0.08rem rgba(250,53,53,0.5);  
		box-shadow: 0 0.0533334rem 0.08rem rgba(250,53,53,0.5);
	}
	.btn a.drop-btn{
		background-color: #21cf77;
		-webkit-box-shadow: 0 0.0533334rem 0.08rem rgba(33,207,119,0.5);  
		-moz-box-shadow: 0 0.0533334rem 0.08rem rgba(33,207,119,0.5);  
		box-shadow: 0 0.0533334rem 0.08rem rgba(33,207,119,0.5);
	}
</style>


