<template>
	<div :class="[options.order ? '':'empty','exposure-box']">
		<div v-if="options.order" class="nempty">
			<div class="exposure-title">
				<p><span class="tip">余额</span><span>{{ options.balance }}</span></p>
				<p><span class="tip">浮动盈亏</span><span>{{ options.isprofit }}{{ options.profit }}</span></p>
				<p><a href="javascript:;" @click="mask()">一键平仓</a></p>
			</div>
			<ExposureList></ExposureList>
		</div>
		<div v-else class="no-order">
			<img src="" alt="">
			<span>您暂无持仓订单</span>
		</div>
		<div class="mask" v-if="options.order && options.mask">
			<div class="auto">
				<h3>一键平仓</h3>
				<p>浮动盈亏：<span :class="options.isrise ? 'rise':'drop'">{{ options.isprofit }}{{ options.profit }}</span></p>
				<p>您确定执行意见平仓操作吗？</p>
				<div class="operation"><a href="javascript:;" name="cnacel" @click="operate($event)">取消</a><a href="javascript:; "name="confirm" @click="operate($event)">确定</a></div>
			</div>
		</div>
	</div>
</template>

<script>
	import	ExposureList from './exposure-list'
	export default{
		components:{
			ExposureList
		},
		data(){
			return{
				options:{
						balance: 3782.65,
						profit: 3782.65,
						isprofit: '+',
						order: true,
						mask: false,
						isrise: true,
				}
			}
		},
		created(){
			let profit = (parseInt(Math.random()*300000)/100).toFixed(2);
			this.options.balance = (parseInt(Math.random()*300000)/100).toFixed(2);
			if(profit>800){
				this.options.profit = profit;
				this.options.isprofit = '+';
				this.options.order = true;
				this.options.isrise = true;
			}else{
				this.options.profit = profit;
				this.options.isprofit = '-';
				this.options.order = false;
				this.options.isrise = false;
			}
		},
		methods:{
			mask(){
				let self = this;
				this.options.mask = true;
				setTimeout(function(){
					console.log(self.options.mask)
				},500)
			},
			operate(e){
				switch(e.target.name){
					case 'cnacel':
						this.options.mask = false;
						break;
					case 'confirm':
						this.options.mask = false;
						break;
				}
			}
		}
	}
</script>

<style scoped>
	  .exposure-box,.nempty{
	    display:-webkit-box;
	    display:-webkit-flex;
	    display:-ms-flexbox;
	    display:flex;
	    -webkit-box-orient:vertical;
	    -webkit-flex-direction:column;
	    -ms-flex-direction:column;
	    flex-direction:column;
	    height:100%;
	  }
	  .exposure-box.empty{
	  	justify-content: center;
	  	align-items: center;
	  }
	  .exposure-list{
	    font-family: 'Avenir', Helvetica, Arial, sans-serif;
	    -webkit-font-smoothing: antialiased;
	    -moz-osx-font-smoothing: grayscale;
	    text-align: center;
	    color: #2c3e50;
	    -webkit-box-flex:1;
	    -webkit-flex:1;
	    -ms-flex:1;
	    flex:1;
	    width:100%;
	    overflow:auto;/* winphone8和android4+ */
	    -webkit-overflow-scrolling: touch; /* ios5+ */
	    background-color: #eee;
	  }
	.exposure-title{
		display:-webkit-box;
	    display:-webkit-flex;
	    display:-ms-flexbox;
	    display:flex;
	    align-items: center;
	    height: 1.6rem;
	    background-color: #FA3535;
	}
	.exposure-title p{
		flex-grow: 1;
	}
	.exposure-title p span{
		display: block;
		font-size: 0.453333rem;
		color: #fff;
	}
	.exposure-title p span.tip{
		font-size: 0.346667rem;
		line-height: 0.72rem;
	}
	.exposure-title p a{
		color: #FA3535;
		font-size: 0.4rem;
		background-color: #fff;
		display: block;
		width: 2.186667rem;
		padding: 0.2rem 0;
		border-radius: 0.266667rem;
		-moz-border-radius: 0.266667rem;
		-webkit-border-radius: 0.266667rem;
		margin: 0 auto;
	}
	.no-order span{
		font-size: 0.373333rem;
		color: #666;
	}
	.mask{
		display:-webkit-box;
	    display:-webkit-flex;
	    display:-ms-flexbox;
	    display:flex;
	    justify-content: center;
	  	align-items: center;
		position: fixed;
		background-color: rgba(0,0,0,0.3);
		width: 100%;
		height: 100%;
		z-index: 10;
	}
	.auto{
		background-color: #fff;
		border-radius: 0.32rem;
		-moz-border-radius: 0.32rem;
		-webkit-border-radius: 0.32rem;
		padding-top: 0.48rem; 
	}
	.auto p{
		margin-top: 0.16rem;
	}
	.auto p:first-of-type{
		color: #666;
	}
	.auto p:first-of-type span.rise{
		color: #fa3535;
	}
	.auto p:first-of-type span.drop{
		color: #21cf77;
	}
	.auto p:last-of-type{
		margin-bottom: 0.2rem;
	}
	.operation a{
		display: inline-block;
		font-size: 0.453333rem;
		padding: 0.338rem 1.2rem;
		color: #000;
		border-top: 1px solid #eaeaea;
	}
	.operation a:first-child{
		border-right: 1px solid #eaeaea; 
	}
</style>


