<template>
	<div class="K-line">
		<ul>
			<li v-for="(Tab,index) in tabs" :class="{ active : Tab.activation}" @click="tab(index)">
				<a href="javascript:;">{{ Tab.title }}</a>
			</li>
		</ul>
		<div id="echarts-box" class="echarts-box"></div>
	</div>
</template>

<script>
	import echarts from 'echarts'
   
	export default{
		name: 'K-line',
		data(){
			return{
				tabs: [
						{title:'分时图',activation:true},
						{title:'5分钟',activation:false},
						{title:'15分钟',activation:false},
						{title:'30分钟',activation:false},
						{title:'60分钟',activation:false}
					],
				isready: true,
				data: [],
				options: {
					data: [Math.random() * 300],
				}
			}
		},
		watch:{
			"options.data": function(){
					if(this.isready){
						this.isready = false
					}else{
						return
					}
					//初始化echarts实例
					let myChart = echarts.init(document.getElementById('echarts-box'));
					// 绘制图表
					let option = null;
					let base = +new Date(1968, 9, 3);
					let oneDay = 24 * 3600 * 1000;
					let date = [];

					let data = this.options.data;
					for (let i = 1; i < 20000; i++) {
					    let now = new Date(base += oneDay);
					    date.push([now.getMonth() + 1, now.getDate()].join('/'));
					    i >= 19999 ? this.isready = true : '';
					}
					option = {
					    xAxis: {
					        type: 'category',
					        boundaryGap: false,
					        data:date
					    },
					    yAxis: {
					        type: 'value',
					        boundaryGap: [0, '100%']
					    },
					    grid: {
							x: 40,
							y: 10,
							x2: 10,
							y2:26
					    },
					    series: [
					        {
					            name:'模拟数据',
					            type:'line',
					            smooth:true,
					            symbol: 'none',
					            sampling: 'average',
					            itemStyle: {
					                normal: {
					                    color: 'rgb(255, 70, 131)'
					                }
					            },
					            areaStyle: {
					                normal: {
					                    color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
					                        offset: 0,
					                        color: 'rgb(255, 158, 68)'
					                    }, {
					                        offset: 1,
					                        color: 'rgb(255, 70, 131)'
					                    }])
					                }
					            },
					            data: data
					        }
					    ]
					};
					if (option && typeof option === "object") {
					    myChart.setOption(option, true);
					}
				}
		},
		mounted(){
				// 绘制图表
				let data = this.options.data;
				for (let i = 1; i < 20000; i++) {
				    data.push(Math.round((Math.random() - 0.5) * 20 + data[i - 1]));
				}
		},
		methods:{
			tab: function(index){
				this.tabs.map(function(a){
					a.activation = false;
				})
				// console.log(this.$children[0].options.data);
				this.tabs[index].activation = true;
				this.data = [Math.random() * 300];
				for (let i = 1; i < 20000; i++) {
				    this.data.push(Math.round((Math.random() - 0.5) * 20 + this.data[i - 1]));
				}
				// this.$children[0].options.data = this.data;
				// console.log(this.$children[0].options.data);
				this.options.data = this.data;
				this.day = index;
			}
		}
	}
</script>
<style scoped>
	.K-line,.K-line ul{
		display: -webkit-box;
		display: -moz-box;
		display: -ms-flexbox;
		display: -webkit-flex;
		display: flex;
		flex-grow: 1;
	}
	.K-line{
		flex-direction: column;
		align-items: center;
		padding: 0.08rem 0.24rem;
	}
	.K-line li{
		border: 1px solid #F5B515;
		border-right-width: 0;
	}
	.K-line li:first-child{
		border-top-left-radius: 8px;
		border-bottom-left-radius: 8px;
	}
	.K-line li:last-child{
		border-right-width: 1px;
		border-top-right-radius: 8px;
		border-bottom-right-radius: 8px;
	}
	.K-line li a{
		font-size: 0.347rem;
		padding: 0.16rem 0.4rem;
		color: #F5B515;
		display: block;
	}
	.K-line li.active{
		overflow: hidden;
	}
	.K-line li.active a{
		background-color: #F5B515;
		color: #fff;
		overflow: hidden;
	}
	.echarts-box{
		width: 100%;
		height: 6.64rem;
	}
</style>


